<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('AddSLA') }}</span>
                    <span class="float-right">
                        <RouterLink to="/Ticketing/Policy" class="p-action-btn text-white float-right">
                            <em class="fa fa-angle-double-left"></em>
                            <br />
                            {{ $t('BacktoList') }}
                        </RouterLink>
                    </span>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit">
                <template v-slot:tgslot-ApplyOnSLA="{ data }">
                    <section class="main-content">
                        <div class="border p-3">
                            <div class="clearfix"></div>
                            <div class="div-scroll">
                                <div class="outer DivRuleFlowDataShowCls flowchart-fix" id="divruleflowdatashow"
                                    v-if="isShowFlowChart">
                                    <ul class="fc_parentchild">
                                        <li class="arrowdown">
                                            <p class="shape_oval shape_ovalwhen">
                                                <span>When</span>
                                            </p>
                                            <ul class="fc_subchild fc_subchildWhen">
                                                <li class="arrowright top50 m-0">
                                                    <div class="formsfieldmainunique">
                                                        <div class="parenthead">
                                                            <span class="text">When do you want to execute the rule ()
                                                                ?</span>
                                                            <span :class="{ 'icon': true, 'd-none': !isShowBackIcon }"
                                                                @click="GotoBack" title="Back" data-event="hide"></span>
                                                        </div>
                                                        <div
                                                            :class="{ 'childdivsunique_edit': true, 'd-none': !isShowEdit }">
                                                            <div class="childdivsuniqueheader">
                                                                <span>Routing: {{ SelectedRouting }} </span>
                                                            </div>
                                                            <div class="childdivediticon_unique textalignright d-block">
                                                                <a class="d-block">
                                                                    <div class="iconedit_unique" @click="EditTarget"></div>
                                                                </a>
                                                            </div>
                                                        </div>
                                                        <div
                                                            :class="{ 'childdivsunique_base': true, 'd-none': isShowEdit }">
                                                            <div class="childdivsunique childdivsunique_HS color757575"
                                                                v-for="(item, index) in RuleEventList" :key="index">
                                                                <input type="radio" name="radioevent"
                                                                    class="clsradioevent d-block" style="display: none;"
                                                                    v-on:change="OnChangeEvent(item, null, null)">
                                                                <label class="offrd"></label>
                                                                <span>{{ item.EVENT_NAME }}</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </li>
                                            </ul>
                                        </li>
                                        <template v-for="(item, index) in AddDeleteTarget"
                                            v-if="AddDeleteTarget.length > 0">
                                            <li class="clstargetadd arrowdown" :id="'li_' + index"
                                                :data-eventid="item.RULE_EVENT_ID">
                                                <p class="shape_polygan">
                                                    <span>{{ $t('Target') }}</span>
                                                    <span class="delete" title="Click here to delete Target"
                                                        @click="DeleteTarget(index)"></span>
                                                </p>
                                                <ul class="fc_subchild">
                                                    <li class="arrowright" :id="'litarger_' + index"
                                                        v-if="TargetRuleCondition == ''">
                                                        <p class="shape_btn">
                                                            <a class="addtarget_btn" v-on:click="AddTarget">{{
                                                                $t('AddTarget') }}</a>
                                                        </p>
                                                    </li>
                                                    <li class="arrowright" :id="'litarger_' + index"
                                                        v-if="TargetRuleCondition != ''">
                                                        <div class="formsfieldmainunique_condition">
                                                            <div class="innercontbase">
                                                                <div class="innercont">{{ TargetRuleCondition }} </div>
                                                                <div class="inneredit d-block">
                                                                    <a class="d-block">
                                                                        <div class="iconedit_unique"
                                                                            @click="EditTargetCondition"></div>
                                                                    </a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </li>
                                                </ul>
                                            </li>
                                        </template>
                                        <li class="arrowdown" v-if="isShowAddMoreTarget">
                                            <p class="shape_roundbtn" title="Click here to add new Target"
                                                @click="AddMoreTarget">
                                                <span>+</span>
                                            </p>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <addtarget :acionId="ActionId" :TargetIndex="TargetIndex" :moduleName="ModuleName" @close="isShowTargetModal = false;"
                            :TargetConditionData="TargetConditionData" @GetformFields="GetformFields"
                            v-if="isShowTargetModal"></addtarget>
                    </section>
                </template>
                <template v-slot:tgslot-Targets="{ data }">
                    <div class="col-lg-12 listing">
                        <div class="table-responsive tableborder table-fix-header">
                            <table class="table table-bordered mb-0" style="width:100%" role="grid">
                                <thead class="thead-bg">
                                    <th width="50" class="text-center"></th>
                                    <th>{{ $t('PriorityType') }} </th>
                                    <th colspan="2" class="text-center" style="min-width:235px;">{{ $t('ResponseTime') }}
                                    </th>
                                    <th colspan="2" class="text-center" style="min-width:240px;">{{
                                        $t('AlertBeforeResponseTimeApproaches') }}</th>
                                    <th colspan="2" class="text-center" style="min-width:235px;">{{ $t('ResolveTime') }}
                                    </th>
                                    <th colspan="2" class="text-center" style="min-width:240px;">{{
                                        $t('AlertBeforeResolveTimeApproaches') }}</th>
                                </thead>
                                <tbody>
                                    <tr v-for="(items, index) in TargetList">
                                        <td>
                                            <div class="custom-control custom-checkbox custom-control-inline" style="">
                                                <input :id="items.prioritY_ID"
                                                    :checked="items.response_time > 0 ? true : false"
                                                    class="chkitems custom-control-input"
                                                    v-on:click="ToggleTargetPriorityCheckboxOnChange(items, $event)"
                                                    type="checkbox" />
                                                <label :for="items.prioritY_ID" type="checkbox"
                                                    class="custom-control-label"></label>
                                            </div>
                                        </td>
                                        <td>
                                            {{ items.prioritY_NAME }}
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResponseTimeField': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResponseTime_' + items.prioritY_ID"
                                                :name="'ResponseTime_' + items.prioritY_ID" :value="items.response_time"
                                                data-val-required="The ResponseTime field is required."
                                                v-model="items.response_time">
                                                <option v-for="item in TimeDropdownList" :value="item.value">{{ item.text }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResponseTimeTypeField': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResponseTimeType_' + items.prioritY_ID"
                                                v-model="items.response_time_type"
                                                :name="'ResponseTimeType_' + items.prioritY_ID"
                                                :value="items.response_time_type">
                                                <option v-for="item in VoilationTimeType" :value="item.value">{{ item.text
                                                }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResponseTimeApproachField': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResponseTimeApproach_' + items.prioritY_ID"
                                                :name="'ResponseTimeApproach_' + items.prioritY_ID" data-val="true"
                                                data-val-required="The ResponseTimeApproach field is required."
                                                v-model="items.response_time_approaches"
                                                :value="items.response_time_approaches">
                                                <option v-for="item in TimeDropdownList" :value="item.value">{{ item.text }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResponseTimeApproachType': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResponseTimeApproachType_' + items.prioritY_ID"
                                                :name="'ResponseTimeApproachType_' + items.prioritY_ID"
                                                v-model="items.response_time_approaches_type"
                                                :value="items.response_time_approaches_type">
                                                <option v-for="item in VoilationTimeType" :value="item.value">{{ item.text
                                                }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResolveTime': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResolveTime_' + items.prioritY_ID"
                                                :name="'ResolveTime_' + items.prioritY_ID" data-val="true"
                                                v-model="items.resolve_time"
                                                data-val-required="The ResolveTime field is required."
                                                :value="items.resolve_time">
                                                <option v-for="item in TimeDropdownList" :value="item.value">{{ item.text }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResolveTimeType': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResolveTimeType_' + items.prioritY_ID"
                                                :name="'ResolveTimeType_' + items.prioritY_ID"
                                                v-model="items.resolve_time_type" :value="items.resolve_time_type">
                                                <option v-for="item in VoilationTimeType" :value="item.value">{{ item.text
                                                }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResolveTimeApproach': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResolveTimeApproach_' + items.prioritY_ID"
                                                :name="'ResolveTimeApproach_' + items.prioritY_ID" data-val="true"
                                                data-val-required="The ResolveTimeApproach field is required."
                                                v-model="items.resolve_time_approaches"
                                                :value="items.resolve_time_approaches">
                                                <option v-for="item in TimeDropdownList" :value="item.value">{{ item.text }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select
                                                :class="{ 'form-control ResolveTimeApproachType': true, 'disabled': (items.isPriorityChecked == false) }"
                                                :disabled="(items.isPriorityChecked == false)"
                                                :id="'ResolveTimeApproachType_' + items.prioritY_ID"
                                                :name="'ResolveTimeApproachType_' + items.prioritY_ID"
                                                v-model="items.resolve_time_approaches_type"
                                                :value="items.resolve_time_approaches_type">
                                                <option v-for="item in VoilationTimeType" :value="item.value">{{ item.text
                                                }}
                                                </option>
                                            </select>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div class="col-12 float-left" v-if="IsRequireTarget"> <span id="spnTarget" style="font-weight: 700;"
                            class="text-danger">{{ $t('SelectTheTarget') }}</span></div>
                </template>
                <template v-slot:tgslot-ViolationTimeReminder="{ data }">
                    <div class="col-lg-12 listing">
                        <div class="table-responsive tableborder table-fix-header overflow-visible">
                            <table class="table table-bordered mb-0" style="width:100%" role="grid">
                                <thead class="thead-bg">
                                    <tr>
                                        <th colspan="3" style="width: 50%; min-width:320px;">{{ $t('TimeApproachedSLA') }}
                                        </th>
                                        <th colspan="2" style="width: 50%;">{{ $t('NotifyTo') }}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td class="Text-center">{{ $t('ResponseDueSLA') }}:</td>
                                        <td class="Text-center">
                                            <select class="form-control ResponseDueSLAField" data-val="true"
                                                data-val-required="The ResponseDueSLA field is required."
                                                id="ResolveTimeApproach" :value="SLAResponseDue" name="ResolveTimeApproach"
                                                v-on:change="ResponseDueSLAOnChange(items, $event)">
                                                <option v-for="item in TimeDropdownList" :value="item.value">{{ item.text }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select class="form-control ResponseDueSLATypeField"
                                                id="ResponseDueSLATypeField" :value="SLAResponseDueType"
                                                name="ResponseDueSLAType"
                                                v-on:change="ResponseDueSLATypeFieldOnChange(items, $event)">
                                                <option :value="item.value" v-for="item in VoilationTimeType">{{ item.text
                                                }}</option>
                                            </select>
                                        </td>
                                        <td class="overflow-visible">
                                            <div class="row">
                                                <div class="col-lg-12">
                                                    <div class="custom-control custom-checkbox custom-control-inline"
                                                        style="">
                                                        <input class="chkitems custom-control-input" id="policySLAReminderResponse_IsHOD"
                                                            name="policySLAReminderResponse_IsHOD" type="checkbox" />
                                                        <label for="policySLAReminderResponse_IsHOD" type="checkbox"
                                                            class="custom-control-label">{{ $t('SLA_HOD') }}</label>
                                                    </div>
                                                    <div class="custom-control custom-checkbox custom-control-inline"
                                                        style="">
                                                        <input class="chkitems custom-control-input" id="policySLAReminderResponse_IsCA"
                                                            name="policySLAReminderResponse_IsCA" type="checkbox" />
                                                        <label for="policySLAReminderResponse_IsCA" type="checkbox"
                                                            class="custom-control-label">{{ $t('SLA_CA') }}</label>
                                                    </div>
                                                    <div class="form-group d-inline-block  mt-0 mb-0 divResponseAssignToIds"
                                                        style="position:absolute; overflow: visible !important;">
                                                        <multiselect placeholder="Select User"
                                                            :max-height="150"
                                                            v-model="ResponseSelectedUser" id="ResponseAssignTo"
                                                            label="text" track-by="value" :limit="2"
                                                            v-bind:options="GetCompanyUserListByCompanyId"
                                                            v-bind:multiple="true" v-bind:taggable="true"></multiselect>
                                                        <input type="hidden"
                                                            asp-for="policySLAReminderResponse.ResponseAssignTo"
                                                            id="hdnResponseAssignTo" />
                                                    </div>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="Text-center">{{ $t('ResolveDueSLA') }}:</td>
                                        <td class="Text-center">
                                            <select class="form-control ResolveDueSLAField" data-val="true"
                                                data-val-required="The ResolveDueSLA field is required." id="ResolveDueSLA"
                                                name="ResolveDueSLA" :value="SLAResolveDue"
                                                v-on:change="ResolveDueSLAOnChange(items, $event)">
                                                <option v-for="item in TimeDropdownList" :value="item.value">{{ item.text }}
                                                </option>
                                            </select>
                                        </td>
                                        <td class="Text-center">
                                            <select class="form-control ResolveDueSLATypeField" id="ResolveDueSLAType"
                                                name="ResolveDueSLAType" :value="SLAResolveDueType"
                                                v-on:change="ResolveDueSLATypeOnChange(items, $event)">
                                                <option :value="item.value" v-for="item in VoilationTimeType">{{ item.text
                                                }}</option>
                                            </select>
                                        </td>
                                        <td class="overflow-visible">
                                            <div class="row">
                                                <div class="col-lg-12">
                                                    <div class="custom-control custom-checkbox custom-control-inline"
                                                        style="">
                                                        <input class="chkitems custom-control-input" id="policySLAReminderResolve_IsHOD"
                                                            name="policySLAReminderResolve_IsHOD" type="checkbox" />
                                                        <label for="policySLAReminderResolve_IsHOD" type="checkbox"
                                                            class="custom-control-label">{{ $t('SLA_HOD') }}</label>
                                                    </div>

                                                    <div class="custom-control custom-checkbox custom-control-inline"
                                                        style="">
                                                        <input class="chkitems custom-control-input" id="policySLAReminderResolve_IsCA"
                                                            name="policySLAReminderResolve_IsCA" type="checkbox" />
                                                        <label for="policySLAReminderResolve_IsCA" type="checkbox"
                                                            class="custom-control-label">{{ $t('SLA_CA') }}</label>
                                                    </div>
                                                    <div class="form-group d-inline-block  mt-0 mb-0 divResolveAssignToIds"
                                                        style="position:absolute; overflow: visible !important;">
                                                        <multiselect placeholder="Select User" v-model="ResolveSelectedUser"
                                                            id="ResolveAssignTo" label="text" track-by="value"
                                                            :max-height="150"
                                                            v-bind:options="GetCompanyUserListByCompanyId" :limit="2"
                                                            v-bind:multiple="true" v-bind:taggable="true"></multiselect>
                                                        <input type="hidden"
                                                            asp-for="policySLAReminderResolve.ResolveAssignTo"
                                                            id="hdnResolveAssignTo" />
                                                    </div>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </template>
            </dynamic-form>
        </div>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import addtarget from "../../components/Modal/modalAddTarget.vue"
export default {
    components: {
        addtarget
    },
    data() {
        return {
            isLoading: false,
            SLAId: 0,
            RuleId: 0,
            ModuleId: 0,
            ActionId: 0,
            EventId: '',
            TargetRuleCondition: '',
            TargetDataBaseCondition: '',
            TargetConditionData: [],
            TargetList: [],
            ResponseSelectedUser: [],
            ResolveSelectedUser: [],
            ListResponseData: [],
            RuleEventList: [],
            TimeReminderList: [],
            VoilationTimeType: [],
            TimeDropdownList: [],
            AddDeleteTarget: [],
            SelectedRouting: '',
            isShowBackIcon: false,
            isShowAddMoreTarget: false,
            IsRequireTarget: false,
            slaResponseReminderId: 0,
            slaResolveReminderId: 0,
            isShowFlowChart: true,
            isShowEdit: false,
            ModuleName: '',
            SLAResponseDue: 0,
            SLAResponseDueType: 'Hour',
            SLAResolveDue: 0,
            SLAResolveDueType: 'Hour',
            GetCompanyUserListByCompanyId: [],
            GetDDLTicketCategoryByCompanyId: [],
            NewOperatorListData: [],
            GetCompanyClientByCompanyId: [],
            GetCompanyUserByCompanyId: [],
            GetDepartmentListbyCompanyForClient: [],
            SlaProductList: [],
            YesorNoList: [],
            GetDDLTicketZoneByCompanyId: [],
            GetWorkTypeList: [],
            isShowTargetModal: false,
            IsAddPolicy: true,
            TargetIndex: 0,
            FormSchema: [
                {
                    layoutType: "triple",
                    group_name: 'Policy Details',
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('PolicyName'),
                            name: "PolicyName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('PolicyDate'),
                            name: "PolicyDate",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('IsApplyTicketPolicy'),
                            name: "IsApplyTicketPolicy",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ]
                            },
                            validationRules: "",
                        }
                    ],
                },
                {
                    layoutType: "single",
                    group_name: 'Apply On SLA',
                    Data: [
                        {
                            astype: "slotField",
                            label: "",
                            name: "ApplyOnSLA",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    group_name: 'SLA Business Hours',
                    Data: [
                        {
                            astype: "toggleField",
                            label: this.$t('24 Hours'),
                            name: "Is24Hours",
                            config: {
                                options: [],
                                onChange: this.GetDisableFields
                            },
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('EventStartTime'),
                            name: "StartTime",
                            value: "",
                            config: {
                                mode: "Time",
                                is24hr: false,
                                format: "YYYY-MM-DD-hh:mm"
                            },
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('CAEndTime'),
                            name: "EndTime",
                            value: "",
                            config: {
                                mode: "Time",
                                is24hr: false,
                                format: "YYYY-MM-DD-hh:mm"
                            },
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('WeekOffDays'),
                            name: "WeekOffDays",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [
                                    {
                                        name: 'Mon', value: "Mon"
                                    }
                                    , {
                                        name: 'Tue', value: "Tue"
                                    }
                                    , {
                                        name: 'Wed', value: "Wed"
                                    }, {
                                        name: 'Thu', value: "Thu"
                                    }, {
                                        name: 'Fri', value: "Fri"
                                    }, {
                                        name: 'Sat', value: "Sat"
                                    }, {
                                        name: 'Sun', value: "Sun"
                                    }, {
                                        name: 'Holiday', value: "Holiday"
                                    }
                                ]
                            },
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    group_name: 'Targets',
                    Data: [
                        {
                            astype: "slotField",
                            label: "",
                            name: "Targets",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    group_name: 'Violation Time Reminder',
                    Data: [
                        {
                            astype: "slotField",
                            label: "",
                            name: "ViolationTimeReminder",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vm = this;
        await this.FetchData();
        await vm.GetWorkTypeListing();
        await this.GetRuleEventItems(vm.ActionId, 0);
        this.ToggleTargetPriorityCheckboxOnChange(this.$route.params.id);
        vm.UserId = vm.GetUserInfo.ID;
        vm.CompanyId = vm.GetUserInfo.CompanyId;
    },
    methods: {
        ToggleTargetPriorityCheckboxOnChange: function (item, e) {
            var vm = this;
            if (e == undefined) {
                vm.TargetList.forEach(function (itm) {
