                    ClosingBrackets: ")",
                    RelationWithParent: itm.condition,
                    FieldId: itm.RuleTarget,
                    OperatorId: itm.OperatorId,
                    RuleCondition: vm.TargetRuleCondition,
                    RuleDatabaseCondition: vm.TargetDataBaseCondition,
                    RuleTargetResultCondition: ""
                };
                ruleTargetList.push(targetObj);
            });
            var targetResult = {
                RuleTargetList: ruleTargetList,
                RuleTargetResult: jsonruleConditionTargetObj
            }
            mainRuleTarget.push(targetResult);
            if (mainRuleTarget[0].RuleTargetList.length == 0) {
                vm.ShowAlert(vm.$t('SlaWhenAlertMessage'), 'warning', true, vm.$t('Alert'));
                vm.buttons[0].class="btn btn-success";
                return false;
            }
            var ListTargetValues = [];
            vm.TargetList.forEach(item => {
                if (item.isPriorityChecked == true) {
                    ListTargetValues.push(item);
                }
            });
            if (ListTargetValues.length == '') {
                var vm = this;
                vm.IsRequireTarget = true;
                vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), 'warning', true, vm.$t('Alert'));
                vm.buttons[0].class="btn btn-success";
                return false;
            } else {
                vm.IsRequireTarget = false;
            }
            ListTargetValues.forEach(item => {
                if (item != "") {
                    var CurrentDateTime = new Date();
                    var responseTime = CurrentDateTime, alertBeforeResonseTime = CurrentDateTime, resolveTime = CurrentDateTime, alertBeforeResolveTime = CurrentDateTime;
                    var txtResponseTime = new Date();
                    if (item.response_time_type == 'Hour') {
                        txtResponseTime = vm.dateAdd(CurrentDateTime, 'HOUR', parseInt(item.response_time))
                    }
                    else if (item.response_time_type == 'Minute') {
                        txtResponseTime = vm.dateAdd(CurrentDateTime, 'Minute', parseInt(item.response_time))
                    }
                    else {
                        txtResponseTime = vm.dateAdd(CurrentDateTime, 'Day', parseInt(item.response_time))
                    }
                    var txtAlertResponseTime;
                    if (item.resolve_time_approaches_type == 'Hour') {
                        txtAlertResponseTime = vm.dateAdd(CurrentDateTime, 'HOUR', parseInt(item.response_time_approaches))
                    }
                    else if (item.resolve_time_approaches_type == 'Minute') {
                        txtAlertResponseTime = vm.dateAdd(CurrentDateTime, 'Minute', parseInt(item.response_time_approaches))
                    }
                    else {
                        txtAlertResponseTime = vm.dateAdd(CurrentDateTime, 'Day', parseInt(item.response_time_approaches))
                    }
                    var txtResolveTime;
                    if (item.resolve_time_type == 'Hour') {
                        txtResolveTime = vm.dateAdd(CurrentDateTime, 'HOUR', parseInt(item.resolve_time))
                    }
                    else if (item.resolve_time_type == 'Minute') {
                        txtResolveTime = vm.dateAdd(CurrentDateTime, 'Minute', parseInt(item.resolve_time))
                    }
                    else {
                        txtResolveTime = vm.dateAdd(CurrentDateTime, 'Day', parseInt(item.resolve_time))
                    }
                    var txtAlertResolveTime;
                    if (item.resolve_time_approaches_type == 'Hour') {
                        txtAlertResolveTime = vm.dateAdd(CurrentDateTime, 'HOUR', parseInt(item.resolve_time_approaches))
                    }
                    else if (item.resolve_time_approaches_type == 'Minute') {
                        txtAlertResolveTime = vm.dateAdd(CurrentDateTime, 'Minute', parseInt(item.resolve_time_approaches))
                    }
                    else {
                        txtAlertResolveTime = vm.dateAdd(CurrentDateTime, 'Day', parseInt(item.resolve_time_approaches))
                    }
                    if (txtResponseTime <= txtAlertResponseTime) {
                        $(this).find('.ResponseTimeField').addClass("is-invalid");
                        vm.ShowAlert(vm.$t('SLAResponseTimeValidation'), 'warning', true, vm.$t('Alert'));
                        vm.buttons[0].class="btn btn-success";
                        msg = "no";
                        chkTargets = true;
                        return false;
                    }
                    if (txtResolveTime <= txtAlertResolveTime) {
                        $(this).find('.ResolveTimeField').addClass("is-invalid");
                        vm.ShowAlert(vm.$t('SLAResolveTimeValidation'), 'warning', true, vm.$t('Alert'));
                        vm.buttons[0].class="btn btn-success";
                        msg = "no";
                        chkTargets = true;
                        return false;
                    }
                    if (txtResponseTime >= txtResolveTime) {
                        vm.ShowAlert(vm.$t('SLAResponseResolveTimeValidation'), 'warning', true, vm.$t('Alert'));
                        $(this).find('.ResolveTimeField').addClass("is-invalid");
                        vm.buttons[0].class="btn btn-success";
                        msg = "no";
                        chkTargets = true;
                        return false;
                    }

                    var oneMinute = 60 * 1000;
                    var ResolveResponseGap = (txtResolveTime - txtResponseTime) / oneMinute;
                    if (ResolveResponseGap < 10) {
                        vm.ShowAlert(vm.$t('SLAMinimumGapResponseResolveTimeValidation'), 'warning', true, vm.$t('Alert'));
                        vm.buttons[0].class="btn btn-success";
                        msg = "no";
                        chkTargets = true;
                        return false;
                    }
                    if (item.response_time_type == 'Minute') {
                        if (parseInt(item.response_time) < 20) {
                            vm.ShowAlert(vm.$t('SLAMinimumResponseTimeValidation'), 'warning', true, vm.$t('Alert'));
                            vm.buttons[0].class="btn btn-success";
                            msg = "no";
                            chkTargets = true;
                            return false;
                        }
                    }
                    if (item.response_time_type == 'Minute') {
                        if (parseInt(item.response_time_approaches) < 10) {
                            vm.ShowAlert(vm.$t('SLAMinimumAlertResponseTimeValidation'), 'warning', true, vm.$t('Alert'));
                            vm.buttons[0].class="btn btn-success";
                            msg = "no";
                            chkTargets = true;
                            return false;
                        }
                    }
                    if (item.response_time_type == 'Minute') {
                        if (parseInt(item.resolve_time) < 30) {
                            vm.ShowAlert(vm.$t('SLAMinimumResolveTimeValidation'), 'warning', true, vm.$t('Alert'));
                            vm.buttons[0].class="btn btn-success";
                            msg = "no";
                            chkTargets = true;
                            return false;
                        }
                    }
                    if (item.response_time_approaches_type == 'Minute') {
                        if (parseInt(item.resolve_time_approaches) < 10) {
                            vm.ShowAlert(vm.$t('SLAMinimumAlertResolveTimeValidation'), 'warning', true, vm.$t('Alert'));
                            vm.buttons[0].class="btn btn-success";
                            msg = "no";
                            chkTargets = true;
                            return false;
                        }
                    }
                }
            });
            var obj = {
                RuleId: vm.RuleId,
                RuleName: formfields.PolicyName,
                ModuleId: vm.ModuleId,
                Description: formfields.Description,
                ActionId: vm.ActionId,
                EventId: vm.EventId,
                RuleTarget: mainRuleTarget,
                IsForSla: 1
            };
            var params = JSON.stringify(obj);
            DataServices.SaveRuleEngine(params).then((response) => {
                if (response.data.STATUS_CODE == 0) {
                    var RuleId = response.data.RULE_ID;
                    vm.AddOrEditPolicy(formfields, RuleId);
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Policy")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        AddOrEditPolicy: async function (formfields, RuleId) {
            var vm = this;
            var data = '';
            var testData = '';
            var ListTarget = [];
            vm.TargetList.forEach(item => {
                if (item.isPriorityChecked == true) {
                    var ListTargetObj = {
                        SlaTargetId: 0,
                        SlaId: 0,
                        CheckBoxEnabled: item.isPriorityChecked,
                        PriorityId: item.prioritY_ID,
                        PriorityName: item.prioritY_NAME,
                        ResponseTime: item.response_time,
                        ResponseTimeType: item.response_time_type,
                        ResolveTime: item.resolve_time,
                        ResolveTimeType: item.resolve_time_type,
                        ResponseTimeApproach: item.response_time_approaches,
                        ResponseTimeApproachType: item.response_time_approaches_type,
                        ResolveTimeApproach: item.resolve_time_approaches,
                        ResolveTimeApproachType: item.resolve_time_approaches_type
                    }
                    ListTarget.push(ListTargetObj);
                }
            })

            var IsHodResolve = $("input[name='policySLAReminderResolve_IsHOD").is(":checked");
            var IsCAResolve = $("input[name='policySLAReminderResolve_IsCA").is(":checked");
            var data = '';
            var dataValue = '';
            vm.ResolveSelectedUser.forEach((item, index) => {
                if (index == 0) {
                    data = item.value;
                    dataValue = {
                        SlaReminderId: 0,
                        AssignId: 0,
                        ReminderAssignId: data
                    }
                }
                else {
                    data += ',' + item.value;
                    dataValue = {
                        SlaReminderId: 0,
                        AssignId: 0,
                        ReminderAssignId: data
                    }
                }
            })
            var ResolveAssignTo = data;
            var listReminderAssignedTo = [];
            var listReminderResolve = dataValue;
            listReminderAssignedTo.push(listReminderResolve);
            if (ResolveAssignTo == "") {
                var PolicySLAReminderResolve;
            } else {
                var PolicySLAReminderResolve = {
                    SlaReminderId: vm.slaResolveReminderId,
                SlaId: 0,
                TimeApproaches: vm.SLAResponseDue,
                TimeApproachesType: vm.SLAResponseDueType,
                ResolveAssignTo: ResolveAssignTo,
                ReminderType: 'Resolve',
                IsHOD: IsHodResolve,
                IsCA: IsCAResolve,
                Emails: '',
                listReminderAssignedTo: listReminderAssignedTo
            };
        }

            var IsHodReminder = $("input[name='policySLAReminderResponse_IsHOD").is(":checked");
            var IsCAReminder = $("input[name='policySLAReminderResponse_IsCA").is(":checked");
            var data = '';
            var dataValue = '';
            vm.ResponseSelectedUser.forEach((item, index) => {
                if (index == 0) {
                    data = item.value;
                    dataValue = {
                        SlaReminderId: 0,
                        AssignId: 0,
                        ReminderAssignId: data
                    }
                }
                else {
                    data += ',' + item.value;
                    dataValue = {
                        SlaReminderId: 0,
                        AssignId: 0,
                        ReminderAssignId: data
                    }
                }
            })
            var ResponseAssignTo = data;
            var listReminderAssignedTo = [];
            var listReminderResponse = dataValue;
            var Is24Hours = formfields.Is24Hours == '' ? false : formfields.Is24Hours;
            listReminderAssignedTo.push(listReminderResponse);
            if(ResponseAssignTo ==""){
                var PolicySLAReminderResponse;
            }else{
            var PolicySLAReminderResponse = {
                SlaReminderId: vm.slaResponseReminderId,
                SlaId: 0,
                TimeApproaches: vm.SLAResolveDue,
                TimeApproachesType: vm.SLAResolveDueType,
                ResponseAssignTo: ResponseAssignTo,
                ReminderType: 'Response',
                IsHOD: IsHodReminder,
                IsCA: IsCAReminder,
                Emails: '',
                listReminderAssignedTo: listReminderAssignedTo
            };
        }
            if(formfields.WeekOffDays=="" || formfields.WeekOffDays ==null){
                var WeekDays= formfields.WeekOffDays;
            }else{
                var Weekdata =formfields.WeekOffDays;
                var dataValue='';
                Weekdata.forEach((itm, index)=>{
                        if(index == 0){
                         dataValue=itm;
                        }
                        else{
                            dataValue += ',' + itm;
                        }
                    })
                    var WeekDays=dataValue;
            }
          
            var obj = {
                ActionId: vm.ActionId,
                ModuleId: vm.ModuleId,
                ApplyOnAll: formfields.IsApplyTicketPolicy[0]=="t"?"true":formfields.IsApplyTicketPolicy[0],
                EventId: vm.EventId,
                Is24Hours: Is24Hours,
                CompanyId: vm.CompanyId,
                CreatedBy: vm.UserId,
                ListApplyOn: [],
                ListTarget: ListTarget,
                PolicyDate: vm.$options.filters.formatDate(formfields.PolicyDate),
                PolicyName: formfields.PolicyName,
                PolicyDescription: formfields.Description,
                RuleId: RuleId,
                SlaId: vm.$route.params.id,
                StatusId: formfields.Status,
                Weekdays: WeekDays,
                StartTime: formfields.StartTime == '' ? null : vm.$options.filters.formatTime(formfields.StartTime),
                EndTime: formfields.EndTime == '' ? null : vm.$options.filters.formatTime(formfields.EndTime),
                PolicySLAReminderResolve: PolicySLAReminderResolve,
                PolicySLAReminderResponse: PolicySLAReminderResponse
            };
            var params = JSON.stringify(obj);
            DataServices.SaveTicketPolicySLA(params).then((response) => {
                if (response.data.status == 'Success') {
                    if (vm.$route.params.id > 0) {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Policy")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Policy")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Policy")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });

        },
    }
}
</script>
